import React, {
    useState,
    useCallback,
    useEffect,
    useLayoutEffect
  } from "react";
  import { Pane } from "evergreen-ui";
  import WriteCharactorSearch from "@components/WriteCharactor/WriteCharactorSearch";
import { async } from "q";
 
  export default function() {
    const [char, setChar] = useState("");
    const [title, setTitle] = useState("");
    const [content, setContent] = useState([]);
    const onSearch = async slug => {
        const fetchingUrl = `https://ytinfo.benjaminlowry.com/${slug}`
        const res = await fetch(fetchingUrl);
        const value = await res.json() as any;
        setChar(slug);
        setTitle(value.player_response.videoDetails.title);
        if(value &&
            value.formats) {
            setContent(value.formats as Array<object>);
        }
    };

    const formatUrl = (url, title)=> {
        url  = url.replace(/\/[^\/]+\.google\.com/, "/redirector.googlevideo.com");
        url = url + `&title=${title}`;
        return url;
    }
  
    useEffect(() => {

    }, [char]);

    return (
      <Pane width="800px" height="100%" margin="auto">
        <WriteCharactorSearch
          char={char}
          onSearch={onSearch}
        ></WriteCharactorSearch>
        {content.map((item, ix) => (
            <div key={ix}>
                <a href={formatUrl(item.url, title)} >download</a>
                <span>{item.resolution}</span>
                <span>{item.container}</span>
            </div>
        ))}
        <style jsx>{`

        `}</style>
      </Pane>
    );
  }
  